import React, { useEffect } from 'react'
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import RocketLaunchIcon from '@mui/icons-material/RocketLaunch';
import Paper from '@mui/material/Paper';
import emailjs from '@emailjs/browser';
import { useState } from 'react';
import LinkedIn from '@mui/icons-material/LinkedIn';
import Instagram from '@mui/icons-material/Instagram';
import GitHub from '@mui/icons-material/GitHub';
import Mail from '@mui/icons-material/Mail';
import { NavLink } from 'react-router-dom';
import IconButton from '@mui/material/IconButton';

const Contact = () => {
    const [btnValue,setBtnValue]=useState("Send Message");
    const [name,setName]=useState("");
    const [senderMail,setSenderMail]=useState("");
    const [message,setMessage]=useState("");
    const sendMail = async (e) => {
        e.preventDefault();
        setBtnValue("Sending...");
        try {
          const response = await fetch('/.netlify/functions/nodemailer', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({senderName:name,senderMail,message}),
          });
    
          if (!response.ok) {
            throw new Error('Failed to send email');
          }
          
          const result = await response.json();
          setBtnValue("Send Message");
          alert(result.message); // Show success message
        } catch (error) {
          console.error('Error sending email:', error);
          alert('Failed to send email. Please try again later.'); // Show error message
          setBtnValue("Send Message");
        }
      };
      useEffect(()=>{
        console.log(name);
        console.log(senderMail);
        console.log(message);
      },[name,senderMail,message])
  return (
    <>
        <Typography variant="h4" sx={{marginBottom:2}}>Contact</Typography>
        {/* <br/> */}
        <Box sx={{backgroundColor:"#fdca66",width:"100px",height:"4px"}}></Box>
        <br/>
        <Typography variant="h5" >Contact form</Typography><br/>
        <Box component="form" onSubmit={sendMail}>
            <Grid container spacing={2}>

                <Grid item xs={12} md={6}>
                    <TextField type="text" required autoComplete="off" placeholder="Full Name" id="from_name" name="from_name" value={name} 
                     InputProps={{style:{borderRadius:10}}} sx={{width:"100%", }}
                     onChange={(e)=>{setName(e.target.value)}}
                     />
                </Grid>
                <Grid item xs={12} md={6}>
                    <TextField type="email" required  autoComplete="off" placeholder="Email address" id="sender_mail" name="sender_mail" value={senderMail} 
                    InputProps={{style:{borderRadius:10}}} sx={{width:"100%", }}
                    onChange={(e)=>{setSenderMail(e.target.value)}}
                    />
                </Grid>
                <Grid item xs={12}>
                    <TextField multiline required autoComplete="off" rows={4} placeholder="Your Message" name="message" value={message} 
                    InputProps={{style:{borderRadius:10}}} sx={{width:"100%", }}
                    onChange={(e)=>{setMessage(e.target.value)}}
                    />
                </Grid>
                
                <Grid item xs={12}  sx={{display:"flex", flexDirection:"row-reverse"}}>
                    <Paper >
                    <Button type="submit" variant="text" endIcon={<RocketLaunchIcon/>} disabled={btnValue=="Sending..."}
                    sx={{backdropFilter:"blur(10px)", background:"hsla(240, 1%, 17%, 0.75)",p:"10px",borderRadius:5,borderTop: '1px solid hsl(0, 0%, 22%)',borderLeft: '1px solid hsl(0, 0%, 22%)'}}
                    >{btnValue}</Button>
                    </Paper>
                </Grid>
                <Grid item xs={12}  sx={{display:"flex", justifyContent:'center',gap:8,color:"#fdca66"}}>
                    
                    <IconButton sx={{color:"#fdca66"}} component={NavLink} target="_blank" to="https://www.linkedin.com/in/dinesh-bala-211a2b22a/"> 
                        <LinkedIn/>
                    </IconButton>
                    <IconButton sx={{color:"#fdca66"}} component={NavLink} target="_blank" to="https://mail.google.com/mail/u/0/#inbox?compose=DmwnWslzBnTgqGMljqSdPFFTzjdXwfPJnpFShwlXvTggQzLHqbgdHFCsclkkmgjxrMrWWzqdHgnq"> 
                        <Mail/>
                    </IconButton>
                    <IconButton sx={{color:"#fdca66"}} component={NavLink} target="_blank" to="https://github.com/DineshBala-A"> 
                        <GitHub/>
                    </IconButton>
                    <IconButton sx={{color:"#fdca66"}} component={NavLink} target="_blank" to="https://www.instagram.com/dineshbala__/"> 
                        <Instagram/>
                    </IconButton>
                </Grid>
            </Grid>

        </Box>
    </>

  )
}

export default Contact