import {React,useRef,useEffect, useState} from 'react'
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import { BrowserRouter as Router, Route, Routes, Link,NavLink } from 'react-router-dom';
import Button from '@mui/material/Button';
import styled from '@emotion/styled';
import Typography from '@mui/material/Typography';
import About from './About/About';
import Resume from './Resume/Resume';
import Projects from './Projects/Projects';
import Contact from './Contact/Contact';

const paths={'/':1,'/resume':2,'/projects':3,'/contact':4};
function MainPage() {
    const urlPath = window.location.pathname;
    // console.log(urlPath);
    // console.log(paths[`${urlPath}`]);
    const [selectedIndex,setSelectedIndex]=useState(paths[`${urlPath}`]);
    const handleClick=(ind)=>{
        setSelectedIndex(ind);
    }
  return (
    <Box sx={{minWidth:{xs:345,sm:0},}}>
    <Router>
    <Box>

        <Box sx={{position:{xs:"sticky",lg:"absolute"},top:{xs:0,lg:0}, right:{lg:0},minWidth:{xs:345,lg:400},background:{xs:"hsla(240, 1%, 17%, 0.9)",lg:"hsla(240, 1%, 17%, 0.75)"},padding:"10px", borderRadius:"0px 20px 0px 20px",
         borderTopLeftRadius:{xs:"20px",lg:0}, borderBottomRightRadius:{xs:"20px",lg:0},backdropFilter:"blur(10px)",display:"flex",flexDirection:'row',justifyContent:"space-evenly",zIndex:10}} >
            <Button component={NavLink} variant='text' sx={{'&:hover': { backgroundColor: 'transparent',opacity:0.5 },color:selectedIndex==1?"#fdca66":"white"}} to="/" onClick={()=>handleClick(1)}>About</Button>
            <Button component={NavLink} variant='text' sx={{'&:hover': { backgroundColor: 'transparent',opacity:0.5 },color:selectedIndex==2?"#fdca66":"white"}} to="/resume" onClick={()=>handleClick(2)}>Resume</Button>
            <Button component={NavLink} variant='text' sx={{'&:hover': { backgroundColor: 'transparent',opacity:0.5 },color:selectedIndex==3?"#fdca66":"white"}} to="/projects" onClick={()=>handleClick(3)}>Projects</Button>
            <Button component={NavLink} variant='text' sx={{'&:hover': { backgroundColor: 'transparent',opacity:0.5 },color:selectedIndex==4?"#fdca66":"white"}} to="/contact" onClick={()=>handleClick(4)}>Contact</Button>
        </Box>
        <Routes>
            <Route path="/"  element={<About/>}/>
            <Route path="/resume" element={<Resume/>}/>
            <Route path="/projects" element={<Projects/>}/>
            <Route path="/contact" element={<Contact/>}/>
            {/* <Route path="/"></Route>
            <Route path="/"></Route>
            <Route path="/"></Route> */}

        </Routes>
    </Box>
    </Router>
    </Box>
  )
}

const Dummy2=()=>{
    return(
        <div>hello2</div>
    )
}

export default MainPage