import React from 'react'
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Timeline from '@mui/lab/Timeline';
import TimelineItem from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineDot from '@mui/lab/TimelineDot';
import { styled } from '@mui/system';
import AutoStoriesIcon from '@mui/icons-material/AutoStories';
import Paper from '@mui/material/Paper';
import EngineeringIcon from '@mui/icons-material/Engineering';
import Skills from './Skills';

function Resume() {

    const CustomTimelineItem=styled(TimelineItem)(({theme})=>({
        '&::before': {
            content: 'none',
            padding:0,
        }
    }));
  return (
    <>
    <Typography variant="h4" sx={{marginBottom:2}}>Resume</Typography>
    {/* <br/> */}
    <Box sx={{backgroundColor:"#fdca66",width:"100px",height:"4px"}}></Box>

    <Timeline position="right" sx={{px:3}}>
      <CustomTimelineItem >
        <TimelineSeparator>
          <TimelineDot  />
          <TimelineConnector sx={{width:"1px",backgroundColor:'grey'}} />
        </TimelineSeparator >
        <TimelineContent>
            <Box sx={{position:"absolute",left:-15,padding:"10px",backdropFilter:"blur(10px)", borderLeft:"1px solid hsl(0, 0%, 22%)",borderTop:"1px solid hsl(0, 0%, 22%)",borderRadius:4}} component={Paper}>   
                <AutoStoriesIcon sx={{color:"#fdca66",width:20,height:20}}/>
            </Box>
            <Typography variant="h5" sx={{marginTop:1,marginLeft:2}}>Education</Typography>
            <br/>
        </TimelineContent>
      </CustomTimelineItem>
      <CustomTimelineItem>
        <TimelineSeparator>
          <TimelineDot sx={{borderWidth:"1px", boxShadow: "0px 0px 0px 3px #343434",background: "#fdca66",}} />
          <TimelineConnector sx={{width:"1px",backgroundColor:'grey'}}  />
        </TimelineSeparator >
        <TimelineContent>
            <Box>
                <Typography variant="body1"> PSNA COllege of Engineering and Technolgoy</Typography>
                <Typography variant="body2" color="text.secondary"> B.Tech Information Technology</Typography>
                <Typography variant="overline" sx={{color:"#fdca66"}}> 2021 - Present</Typography>
            </Box>
        </TimelineContent>
      </CustomTimelineItem>
      <CustomTimelineItem>
        <TimelineSeparator>
          <TimelineDot sx={{borderWidth:"1px", boxShadow: "0px 0px 0px 3px #343434",background: "#fdca66",}} />
        </TimelineSeparator >
        <TimelineContent>
            <Box>
                <Typography variant="body1">H.N.U.P.R Matric Higher Secondary School</Typography>
                <Typography variant="body2" color="text.secondary"> HSC - Computer Science </Typography>
                <Typography variant="overline" sx={{color:"#fdca66"}}> 2020 - 2021</Typography>
            </Box>
        </TimelineContent>
      </CustomTimelineItem>
    </Timeline>
    <Timeline position="right" sx={{px:3}}>
      <CustomTimelineItem >
        <TimelineSeparator>
          <TimelineDot  />
          <TimelineConnector sx={{width:"1px",backgroundColor:'grey'}} />
        </TimelineSeparator >
        <TimelineContent>
            <Box sx={{position:"absolute",left:-15,padding:"10px",backdropFilter:"blur(10px)", borderLeft:"1px solid hsl(0, 0%, 22%)",borderTop:"1px solid hsl(0, 0%, 22%)",borderRadius:4}} component={Paper}>   
                <EngineeringIcon sx={{color:"#fdca66",width:20,height:20}}/>
            </Box>
            <Typography variant="h5" sx={{marginTop:1,marginLeft:2}}>Experience</Typography>
            <br/>
        </TimelineContent>
      </CustomTimelineItem>
      <CustomTimelineItem>
        <TimelineSeparator>
          <TimelineDot sx={{borderWidth:"1px", boxShadow: "0px 0px 0px 3px #343434",background: "#fdca66",}} />
          {/* <TimelineConnector sx={{width:"1px",backgroundColor:'grey'}}  /> */}
        </TimelineSeparator >
        <TimelineContent>
            <Box>
                <Typography variant="body1"> CISCO - Virtual Internship</Typography>
                <Typography variant="overline" sx={{color:"#fdca66"}}> June 2023 - July 2023</Typography>
                <Typography variant="body2" color="text.secondary"> 
                Acquired comprehensive networking knowledge, demonstrating proficiency in Cisco's Packet Tracer, and expertise in network design and optimization.</Typography>

            </Box>
        </TimelineContent>
      </CustomTimelineItem>
    </Timeline>

    <Skills/>

    </>
  )
}

export default Resume