import React from 'react'
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import ProfileCard from './Profile/ProfileCard';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import MainPage from './MainScreen/MainPage';

function Screen() {
  return (
    <>
    <Container sx={{display:"flex", justifyContent:'center',alignItems:'center',height:{lg:'100vh'}, maxWidth:{xs:'100%',sm:'90%', md:'70%',lg:'80%',xl:"80%"},p:{xs:1,lg:0,xl:"inherit",}}}>
        {/* sx={{display:"flex", flexDirection:{xs:'column',sm:'row',md:'row',lg:'row'}, gap:{md:10}}} */}
        <Grid container spacing={2} > 
            <Grid item xs={12} lg={3}>
                <ProfileCard/>
            </Grid>
            
            <Grid item xs={12} lg={9} >
                <Paper sx={{minWidth:{xs:345,sm:0},p:'10',height:{xs:'fit-content',lg:'90vh'},maxWidth: {xs:'100%',sm:800,md:1000,xl:1200 },background: 'hsl(240deg 2% 12% / 88%)',
                border: '1px solid hsl(0, 0%, 22%)',borderRadius: '20px',padding:"20px",position:{xs:'static',lg:"relative"},backdropFilter:"blur(10px)",top:0,overflowY:{lg:'scroll'},
                '&::-webkit-scrollbar': {
                    display: 'none',
                  },
                  '-ms-overflow-style': 'none', /* IE and Edge */
                  'scrollbar-width': 'none',}}>
                    <MainPage/>
                </Paper>
            </Grid>
        </Grid>
    </Container>

    </>
  )
}

export default Screen