const ProjectCard=({url,title,desc,urlWithPathList,detailedDesc,link})=>{
    const [open, setOpen]=useState(false);
    useEffect(()=>{
        console.log(open);
    },[open])

    const handleClose = () => {
        setOpen(false);
    };
    return (
        <>
            <Card sx={{ background:"transparent", boxShadow:0,borderRadius:5,}} onClick={()=>setOpen(!open)}>
                <CardActionArea >
                    <CardMedia
                    sx={{border:"1px solid hsl(0, 0%, 22%)", borderRadius:5,}}
                    height="140"
                    component="img"
                    image={url}
                    />
                    <CardContent>
                    <Typography gutterBottom variant="body1" component="div">
                        {title}
                    </Typography>
                    {/* <Typography variant="body2" color="text.secondary">
                       {desc}
                    </Typography> */}
                    </CardContent>
                </CardActionArea>
            </Card>
            <Dialog maxWidth={'md'} open={open} onClose={handleClose} PaperProps={{sx:{backgroundImage: "linear-gradient(#9e9e9e29,#9e9e9e29)", background:'hsl(240deg 2% 12% / 88%)',border: '1px solid hsl(0, 0%, 22%)',backdropFilter:"blur(10px)",borderRadius:5}}}>
                <DialogTitle sx={{ m: 0, p: 2 }} id="customized-dialog-title">
                {title}
                </DialogTitle>
                <IconButton
                aria-label="close"
                onClick={handleClose}
                sx={{
                    position: 'absolute',
                    right: 8,
                    top: 8,
                    color: (theme) => theme.palette.grey[500],
                }}
                >
                <CloseIcon />
                </IconButton>
                <DialogContent dividers sx={{'&::-webkit-scrollbar': {
                    display: 'none',
                  },
                //   '-ms-overflow-style': 'none', /* IE and Edge */
                  'scrollbar-width': 'none',}}>
                    <ImageCarousel title={title} urlWithPathList={urlWithPathList} detailedDesc={detailedDesc}/>
                <Box>{detailedDesc}</Box>
                </DialogContent>
                <DialogActions>
                <Button autoFocus endIcon={<LaunchIcon/>} component={NavLink} to={link} target='_blank'>
                    Visit
                </Button>
                </DialogActions>
            </Dialog>
        </>
    )
}


  
  const ImageCarousel = ({title, urlWithPathList, detailedDesc}) => {
    const [activeStep, setActiveStep] = useState(0);
    const theme = useTheme();
    const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));
    if(urlWithPathList==null || urlWithPathList.length==0)return(<></>);
    const maxSteps = Math.ceil(urlWithPathList.length / (isSmallScreen ? 1 : 2));
  
    const handleNext = () => {
      setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };
  
    const handleBack = () => {
      setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };
  
    const handleStepChange = (step) => {
      setActiveStep(step);
    };
  
    return (<>
        {urlWithPathList!=null && urlWithPathList.length>0?(
      <Box sx={{ maxWidth: 800, flexGrow: 1 }}>
        <SwipeableViews
          axis={'x'}
          index={activeStep}
          onChangeIndex={handleStepChange}
          enableMouseEvents
        >   
        {/* <Paper
            square
            elevation={0}
            sx={{
              display: 'flex',
              alignItems: 'center',
              height: 50,
              pl: 2,
              backgroundImage: "linear-gradient(#9e9e9e29,#9e9e9e40)", background:'hsl(240deg 2% 12% / 88%)'
            }}
          >
          </Paper> */}
          {Array.from(new Array(maxSteps)).map((_, index) => (<>
            <Grid container key={index} spacing={2}>
              {urlWithPathList.slice(index * (isSmallScreen ? 1 : 2), index * (isSmallScreen ? 1 : 2) + (isSmallScreen ? 1 : 2)).map((step, subIndex) => (
                <Grid item xs={12} sm={6} key={subIndex}>
                  <Box
                    component="img"
                    sx={{
                      height: 255,
                      display: 'block',
                      maxWidth: 400,
                      overflow: 'hidden',
                      width: '100%',
                    }}
                    src={step.imgPath}
                    alt={step.label}
                  />
                </Grid>
              ))}
            </Grid></>
          ))}
        </SwipeableViews>
        <MobileStepper
        style={{backgroundImage: "linear-gradient(#9e9e9e40,#9e9e9e40)", background:'hsl(240deg 2% 12% / 88%)'}}
          steps={maxSteps}
          position="static"
          activeStep={activeStep}
          nextButton={
            <Button
              size="small"
              onClick={handleNext}
              disabled={activeStep === maxSteps - 1}
            >
              Next
              {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
            </Button>
          }
          backButton={
            <Button size="small" onClick={handleBack} disabled={activeStep === 0} >
              {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
              Back
            </Button>
          }
        />
      </Box>):
      <></>}
      </>
    );
  };
export default projects