import React from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import Box from '@mui/material/Box';
import Card from   '@mui/material/Card';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import ArrowBackIos from '@mui/icons-material/ArrowBackIos';
import ArrowForwardIos from '@mui/icons-material/ArrowForwardIos';
import IconButton from '@mui/material/IconButton';
import Grid from '@mui/material/Grid';



const images = [
    'https://www.netlify.com/favicon/icon.svg',
    './assets/svg/mui.svg',
    "https://reactnative.dev/img/favicon.ico",
   'https://upload.wikimedia.org/wikipedia/commons/c/c3/Python-logo-notext.svg',
  'https://upload.wikimedia.org/wikipedia/commons/6/6a/JavaScript-logo.png',
  'https://upload.wikimedia.org/wikipedia/en/3/30/Java_programming_language_logo.svg',
  'https://upload.wikimedia.org/wikipedia/commons/1/19/C_Logo.png',
  'https://upload.wikimedia.org/wikipedia/commons/1/18/ISO_C%2B%2B_Logo.svg',
  'https://upload.wikimedia.org/wikipedia/commons/a/a7/React-icon.svg',
  'https://upload.wikimedia.org/wikipedia/commons/d/d9/Node.js_logo.svg',
  'https://upload.wikimedia.org/wikipedia/commons/6/64/Expressjs.png',
  'https://upload.wikimedia.org/wikipedia/commons/6/61/HTML5_logo_and_wordmark.svg',
  'https://upload.wikimedia.org/wikipedia/commons/d/d5/CSS3_logo_and_wordmark.svg',
  'https://www.netlify.com/v3/img/components/logomark.png',
  'https://upload.wikimedia.org/wikipedia/commons/2/27/PHP-logo.svg',
  'https://upload.wikimedia.org/wikipedia/commons/0/0a/MySQL_textlogo.svg',
  'https://www.mongodb.com/assets/images/global/favicon.ico',
  'https://upload.wikimedia.org/wikipedia/commons/3/33/Figma-logo.svg',
  'https://upload.wikimedia.org/wikipedia/commons/e/e0/Git-logo.svg',
  'https://github.githubassets.com/favicons/favicon-dark.png',
  'https://static.djangoproject.com/img/logos/django-logo-negative.svg',
  'https://img.icons8.com/?size=100&id=ewGOClUtmFX4&format=png&color=000000',
  'https://reactnativepaper.com/static/favicon-362e6c01ca343fc56f8a817738ecdacd.ico',
  'https://cordova.apache.org/static/img/cordova_bot.png'

  ];

  const ImageCarousel = () => {
    // Slick settings
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: Math.floor(window.innerWidth / 200), // Adjust according to your image size
      slidesToScroll: 1,
      swipeToSlide: true,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: Math.floor(window.innerWidth / 300),
          },
        },
        {
          breakpoint: 768,
          settings: {
            slidesToShow: Math.floor(window.innerWidth / 300),
          },
        },
      ],
    };
  
    return (
      <div className="scrollable-carousel">
        <Box sx={{p:3,}}>
        <Slider {...settings}>
          {images.map((image, index) => (
            <Grid item key={index}>
              {/* <Card sx={{ maxWidth: 345 ,background:'none',backgroundImage:'none'}}> */}
                <CardMedia
                  component="img"
                  height="80"
                  image={image}
                  alt={`Image ${index}`}
                  style={{ objectFit: 'contain' }} // Adjust objectFit as needed
                />
              {/* </Card> */}
            </Grid>
          ))}
        </Slider>
        </Box>
      </div>
    );
  };
  
  export default ImageCarousel;